.menu-central {
  height: 70px;
  display: flex;
  align-items: center;
  background-color: var(--bg-card);
  box-shadow: 0 2px 4px var(--shadow-color);
  border-bottom: 1px solid var(--border-color);
  transition: background-color 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}
.navbar .dropdown-toggle::after {
  display: none;
}
.logo {
  padding: 0 10px;
  width: 300px;
  margin-right: 20%;
  font-weight: bold;
  font-size: 1.5rem;
}
.item-menu-central {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
}
.item-menu-central li {
  margin: 0 20px;
  position: relative;
}
.item-menu-central .nav-link, .item-menu-central .nav-link2 {
  padding: 10px 20px;
  text-decoration: none;
  font-size: 19px;
  color: var(--text-primary);
  border-radius: 30px;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}
.item-menu-central .nav-link:hover {
  background-color: var(--bg-input);
  color: var(--text-primary) !important;
}
.dropdown-container {
  position: relative;
}
.dropdown-toggle {
  cursor: pointer;
}
.custom-dropdown {
  position: absolute;
  right: 0;
  top: 100%;
  background: var(--bg-card);
  border-radius: 5px;
  box-shadow: 0 2px 10px var(--shadow-color);
  border: 1px solid var(--border-color);
  min-width: 180px;
  z-index: 1000;
  overflow: hidden;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}
.dropdown-item {
  padding: 10px 15px;
  display: block;
  color: var(--text-primary);
  text-decoration: none;
  transition: all 0.2s;
  border-bottom: 1px solid var(--border-color);
}
.dropdown-item:last-child {
  border-bottom: none;
}
.dropdown-item:hover {
  background-color: var(--bg-input);
  color: var(--text-primary);
}
.text-danger {
  color: #dc3545 !important;
}
.dropdown-item.text-danger:hover {
  background-color: #ff8894;
  color: black !important;
}
.dropdown-toggle .nav-link2 {
  padding: 10px 15px;
}
.dropdown-toggle .nav-link2 span {
  display: flex;
  align-items: center;
}
.dropdown-toggle .nav-link2 svg {
  transition: transform 0.2s ease;
}
@media (max-width: 992px) {
  .menu-central {
    height: auto;
    padding: 10px 0;
  }
  .item-menu-central {
    flex-direction: column;
    align-items: flex-start;
  }
  .item-menu-central li {
    margin: 5px 0;
    width: 100%;
  }
  .item-menu-central .nav-link {
    border-radius: 5px;
    width: 100%;
  }
  .custom-dropdown {
    position: static;
    box-shadow: none;
    width: 100%;
  }
}
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f9f9f9;
  text-align: center;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
header .menu {
  display: flex;
  align-items: center;
}
.menu-btn {
  font-size: 24px;
  border: none;
  background-color: #007bff;
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 10px;
}
header h1 {
  font-size: 24px;
  color: #000;
}
header h1 span {
  color: #ff0000;
}
header .user {
  display: flex;
  align-items: center;
}
header .user-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-left: 10px;
}
.buttons-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  padding: 20px;
}
.blue-btn {
  background-color: #007bff;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
  padding: 12px 16px;
  border: none;
  border-radius: 10px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  text-transform: uppercase;
  box-sizing: border-box;
  max-width: 100%;
  overflow: hidden;
}
.blue-btn:hover {
  background-color: #0056b3;
}
.blue-btn br {
  display: block;
}
.character img {
  width: 150px;
  margin: 20px 0;
}
.character p {
  font-size: 18px;
  font-weight: bold;
}
footer {
  display: flex;
  justify-content: center;
  gap: 10px;
  padding: 10px;
  background-color: #f1f1f1;
  border-top: 1px solid #ddd;
}
.footer-btn {
  background-color: #e0e0e0;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
}
.footer-btn:hover {
  background-color: #d0d0d0;
}
.container-main {
  margin-left: 7px;
  margin-top: 8px;
}
div .item-menu-central {
  display: flex;
  align-self: center;
}
.item-menu-central a {
  font-size: 1.1vw;
}
nav ul {
  list-style: none;
}
nav ul li {
  display: inline-block;
}
nav ul li a {
  padding: 10px 20px;
  text-decoration: nome;
  font-size: 19px;
  color: black;
  border-radius: 30px;
  transition: 0.6s;
}
a:link {
  text-decoration: none;
}
.body-wrapper {
  display: flex;
  align-items: start;
}
.body-wrapper > .container {
  margin-top: 0 !important;
}
.body-wrapper .main {
  display: flex;
  flex-direction: row;
}
.btn-expandir {
  width: 100%;
  padding-left: 10px;
}
.btn-expandir > i {
  color: #ffffff;
  font-size: 30px;
  cursor: pointer;
}
ul.item-menu {
  height: 100%;
  list-style-type: none;
  padding: 0;
}
ul li.item-menu a {
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  padding: 20px 4px;
  display: flex;
  margin-bottom: 20px;
  line-height: 40px;
  gap: 0 16px;
}
ul li.item-menu a.txt-link {
  margin-left: 40px;
}
ul li.item-menu a .icon > i {
  font-size: 30px;
  margin-left: 10px;
}
ul li .txt-link {
  display: none;
}
nav.menu-lateral:hover > .item-menu .txt-link {
  display: block;
}
.w-300 {
  width: 300px !important;
}
@media (min-width: 992px) {
  nav.menu-lateral:hover {
    width: 300px;
  }
}
