.custom-dropdown {
  position: absolute;
  right: 0;
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  box-shadow: 0 2px 10px var(--shadow-color);
  z-index: 1000;
  min-width: 200px;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}
.dropdown-item {
  padding: 8px 16px;
