  transition: color 0.3s ease, background-color 0.3s ease;
}
.dropdown-item:hover {
  background-color: #00a2ff;
  text-decoration: none;
}
@keyframes slideDownAndFade {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
